
















let observer = null;
export default {
	data() {
		return {
			appear: false,
			title:'intersectionObserver'
		}
	},
	onReady() {
		observer = uni.createIntersectionObserver(this);
		observer.relativeTo('.scroll-view').observe('.ball', (res) => {
			if (res.intersectionRatio > 0 && !this.appear) {
				this.appear = true;
			} else if (!res.intersectionRatio > 0 && this.appear) {
				this.appear = false;
			}
		})
	},
	onUnload() {
		if (observer) {
			observer.disconnect()
		}
	}
}
